import React, { Fragment, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import "./Login.css";
import logo from "../../images/logo.png";
import swal from 'sweetalert';
import axios from 'axios';
import Cookies from 'universal-cookie';



const Login = () => {
  //local 
  const baseUrl = "http://localhost:30010/api/usuarios";

  //produccion          
  //const baseUrl = "https://recursosrest.herokuapp.com/api/usuarios";

  const cookies = new Cookies();

  const [credenciales, setCredenciales] = useState({
    email: "",
    password: "",
  });


  const handleInputChange = (event) => {
    // console.log(event.target.name)
    // console.log(event.target.value)
    setCredenciales({
      ...credenciales,
      [event.target.name]: event.target.value,
    });
  };

  const iniciarSesion = async (e) => {

    e.preventDefault();

    if (credenciales.email.length <= 0) {
      swal({
        title: "¡Campo vacio!",
        text: "Ingresa tu correo electrónico",
        icon: "warning",
        button: "Aceptar",
        timer: "2000"

      });
    } else if (credenciales.password.length <= 0) {
      swal({
        title: "¡Campo vacio!",
        text: "Ingresa tu contraseña",
        icon: "warning",
        button: "Aceptar",
        timer: "2000"
      });
    } else {
      await axios(baseUrl + `/${credenciales.email}/${credenciales.password}`)
        .then(response => {

          const savedata = response.data;
          const datauser = (Object.keys(response.data).length);

          if (datauser > 0) {

            cookies.set('id_usuario', savedata.id_usuario, { path: "/" });
            cookies.set('nombre_completo_usuario', savedata.nombre_completo_usuario, { path: "/" });
            cookies.set('edad_usuario', savedata.edad_usuario, { path: "/" });
            cookies.set('telefono_usuario', savedata.telefono_usuario, { path: "/" });
            cookies.set('correo', savedata.correo, { path: "/" });
            cookies.set('nombre_cargo', savedata.nombre_cargo, { path: "/" });
            cookies.set('id_permiso_usuario', savedata.id_permiso_usuario, { path: "/" });

            window.location.href = "./Dashboard";


          } else {
            swal({
              title: "¡Aviso!",
              text: "El usuario o la contraseña no son correctos",
              icon: "warning",
              button: "Aceptar",
              timer: "4000"
            });

          }

        })

        .catch(error => {
          console.log(error);
        })

    }

  }

  return (
    <Fragment>
      {cookies.get('correo') && <Redirect to="./dashboard" />}
      <div className="fondo-login">
        <div className="content-login">
          <img src={logo} alt="Logo pollo rico" />
          <h1>Iniciar Sesión</h1>
          <form onSubmit={iniciarSesion}>
            <label id="label">Correo</label>
            <input
              id="email"
              name="email"
              type="email"
              placeholder="Correo electrónico"
              required
              onChange={handleInputChange}
            ></input>
            <label id="label">Contraseña</label>
            <input
              id="password"
              name="password"
              type="password"
              placeholder="Contraseña"
              required
              onChange={handleInputChange}
            ></input>
            <input
              onClick={iniciarSesion}
              id="send"
              type="submit"
              value="Ingresar"
            ></input>
            <Link id="reset-password" to="/ForgotPassword">
              Olvidé mi contraseña
            </Link>

          </form>
        </div>
      </div>
    </Fragment>
  );
};

export default Login;
