import React, { Fragment, useState } from "react";
import logo_email from "../../images/email.png";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import "./ForgotPassword.css";
import swal from 'sweetalert';
import axios from 'axios';
import Cookies from 'universal-cookie';
import { Link, Redirect } from "react-router-dom";


const ForgotPassword = () => {

  //local
  const baseUrl = "http://localhost:30010";


  //produccion     
  //const baseUrl = "https://recursosrest.herokuapp.com";

  const cookies = new Cookies();

  const [email_input, setEmail_input] = useState({
    email: "",
  });

  const [enviado, setEnviado] = useState(true);

  const stylesde = { background:"rgb(99, 125, 245)", color:"rgb(219, 219, 219)", cursor: "context-menu" };
  const stylesac = {  };

  const handleChange = (event) => {
    // console.log(event.target.name)
    // console.log(event.target.value)
    setEmail_input({
      ...email_input,
      [event.target.name]: event.target.value,
    });
  };


  const recuperarContrasena = async (e) => {
    e.preventDefault();

    if(email_input.email.length <= 0) {
        swal({
            title: "¡Campo vacio!",
            text: "¡Ingresa tu correo electrónico!",
            icon: "warning",
            button: "Aceptar",
            timer: "3000"
        });
    } else{
      setEnviado(false);
      await axios(baseUrl + `/obtenerpass/${email_input.email}`)
        .then(response => {

            const savedata = response.data;
            const datauser = (Object.keys(response.data).length);

            if (datauser > 0) {
                
                cookies.set('contrasena', savedata.contrasena, { path: "/" });
                let pass = cookies.get('contrasena');

                cookies.set('nombre_completo_usuario', savedata.nombre_completo_usuario, { path: "/" });
                let nom_usu = cookies.get('nombre_completo_usuario');
                
                axios.post(baseUrl + '/resetpassword', { 
                    nombre: nom_usu,
                    email: email_input.email,
                    password: pass
                }).then(response => {
                  setEnviado(true);
                  swal({
                    title: "¡Contraseña enviada!",
                    text: "¡La contraseña fue enviada a tu correo electrónico!, sino encuentra el correo verificar en sección de correos no deseados o Spam",
                    icon: "success",
                    button: "Aceptar",
                    timer: "6000"
                });
                }).catch(e => {
                    console.log(e);
                });
                

            } else {
                swal({
                    title: "¡Aviso!",
                    text: "¡El correo ingresado no se encuentra registrado!",
                    icon: "warning",
                    button: "Aceptar",
                    timer: "4000"
                });

            }

        })
        
        .catch(error => {
            console.log(error);
        })
    }

   

    

}


  return (
    <Fragment>
      {cookies.get('correo') && <Redirect to="./dashboard" />} 
      <Header />
      <div className="fondo-forgot-password">
        <div className="content-forgot-password">
          <form onSubmit={recuperarContrasena} className="form">
            <img src={logo_email} alt="Logo_email" />
            <h1>¿Olvidaste tu contraseña?</h1>
            <p>
              ¡Ingresa el correo electrónico que usaste al momento de{" "}
              registrarte; para recuparar tu contraseña!
            </p>
            <label id="label">Correo</label>
            
            <input 
              id="email"
              name="email"
              type="email"
              placeholder="Correo electrónico"
              required
              onChange={handleChange}
            />
           
            <input style={enviado ? stylesac : stylesde} onClick={recuperarContrasena} disabled={enviado ? "" : "true"} id="send" type="submit" value="Enviar" />
          </form>
        </div>
      </div>
      <footer className="footer">
        <Link to="/Login">
          <h3>
            {" "}
            <i className="fas fa-arrow-left"></i>Regresar
          </h3>
        </Link>
      </footer>
      <Footer />
    </Fragment>
  );
};

export default ForgotPassword;
